<template>
  <div class="demo">
    <div class="fun-title">精选案例</div>
    <div class="demo-box">
      <div class="demo-item" v-for="item in demoList" :key="item.id">
        <img :src="item.url" alt="">
      </div>
    </div>
  </div>
  
</template>

<script>
// 洛阳市畅易阁文化传播有限公司
  import demo1 from "../../../assets/image/demo1.jpg";
  import demo2 from "../../../assets/image/demo2.jpg";
  import demo3 from "../../../assets/image/demo3.jpg";
  import demo4 from "../../../assets/image/demo4.jpg";
  import demo5 from "../../../assets/image/demo5.jpg";
  import demo6 from "../../../assets/image/demo6.jpg";
  import demo7 from "../../../assets/image/demo7.jpg";
  import demo8 from "../../../assets/image/demo8.jpg";

  export default {
    data() {
      return {
        demoList: [
          { id: 1, url: demo1 },
          { id: 2, url: demo2 },
          { id: 3, url: demo3 },
          { id: 4, url: demo4 },
          { id: 5, url: demo5 },
          { id: 6, url: demo6 },
          { id: 7, url: demo7 },
          { id: 8, url: demo8 }
        ]
      }
    },
    comments:{

    },
    mounted() {

    },
    methods:{

    }
  }
</script>

<style scoped>
/* #00a9fe */
 .fun-title {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 60px;
    font: 26px/80px '微软雅黑';
    font-weight: bold;
    /* color:#00a9fe; */
    color:#333;
    text-align: center;
  }
  .demo-box {
    margin: 0 auto;
    width: 1200px;
    min-height: 200px;
    overflow: hidden;
  }
  .demo-item {
    margin-top: 20px;
    float: left;
    width: 25%;
    height: 550px;
  }
  .demo-item > img {
    margin: 0 auto;
    display: block;
    width: 90%;
    height: 100%;

  } 
  

</style>


