<template>
  <div class="banner">
    <el-carousel trigger="click" :interval=interval height="550px" style="overflow: hidden;">
      <el-carousel-item v-for="item in bannerList" :key="item.id">
        <img :src="item.url" class="ban-img" alt="畅易阁">
      </el-carousel-item>
    </el-carousel>
  </div>
  
</template>

<script>
// 洛阳市畅易阁文化传播有限公司
  import ban01 from "../../../assets/image/ban01.jpg";
  import ban02 from "../../../assets/image/ban02.jpg";
  import ban03 from "../../../assets/image/ban03.jpg";
  import ban04 from "../../../assets/image/ban04.jpg";


  export default {
    data() {
      return {
        // 导航
        interval: 4000,
        bannerList: [
          { id: 1001, url: ban01 },
          { id: 1002, url: ban02 },
          { id: 1003, url: ban03 },
          { id: 1004, url: ban04 }
        ],
      }
    },
    comments:{

    },
    mounted() {

    },
    methods:{

    }
  }
</script>

<style scoped>
/* #00a9fe */
  .banner {
    width: 100%;
    min-height: 420px;
  }
  .ban-img {
    display: block;
    width: 100%;
    height: 100%;
  }

</style>


