<template>
  <div class="about">
    <div class="fun-title">关于畅易阁文化</div>
    <div class="ab-details">
      {{message}}
    </div>
    <div class="ab-details">
      {{detail}}
    </div>
  </div>
  
</template>

<script>
// 洛阳市畅易阁文化传播有限公司


  export default {
    data() {
      return {
        message: "洛阳市畅易阁文化传播有限公司成立于2019年，是一家从事与易学相关的科研、培训、咨询、策划、文化交流的专业命理分析平台。公司拥有一支实力强大的易学专家团队，以弘扬中华优秀传统文化为使命，秉承“正本清源、立易正心”的理念，致力于传播易学正能量，利用易学服务广大客户。",
        detail: "《周易》是中国传统文化的源头，其倡导的“天行健，君子以自强不息；地势坤，君子以厚德载物”更是中国传统文化的精髓。古人云，凡事预则立，不预则废。周易预测就是指对一切未知事物及其演变规律作出的合乎科学的推断。荷兰预测家波拉克说:“这是一门研究未来，创造未来的科学。它的任务就是要向人们揭示社会生活各个领域未来发展的前景从而提醒人们对具有多种可能性的未来社会及早作出抉择。”我们的事业是走向未来的事业;走向未来的事业离不开揭示未来的理论，只有科学的预测未来，才能适应未来，创造未来，更好地把握今天。"
      }
    },
    comments:{

    },
    mounted() {

    },
    methods:{

    }
  }
</script>

<style scoped>
/* #00a9fe */
 .fun-title {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 60px;
    font: 26px/80px '微软雅黑';
    font-weight: bold;
    /* color:#00a9fe; */
    color:#333;
    text-align: center;
  }
  .ab-details {
    margin: 0 auto;
    width: 1000px;
    font: 18px/40px "微软雅黑";
    color: #727272;
    text-indent: 32px;


  }

</style>


