<template>
  <div class="warp">
    <header class="header">
      <div class="header-logo">
        <img :src="logo" alt="畅易阁" class="logo-img">
      </div>
      <div class="header-company">畅易阁文化传播有限公司</div>
      <ul class="header-nav">
        <li :class="{headerLi:true, active: activeIndex == item.id}" v-for="item in navList" :key="item.id">{{item.name}}</li>
      </ul>
    </header>
  </div>
  
</template>

<script>
// 洛阳市畅易阁文化传播有限公司
  import logo from "../../../assets/image/logo.jpg";

  export default {
    data() {
      return {
        // 导航
        navList: [
          { id: 1001, name: '畅易阁'},
          { id: 1002, name: '主营业务'},
          { id: 1003, name: '关于我们'},
          { id: 1004, name: '成功案例'},
          { id: 1005, name: '联系我们'},
        ],
        logo: logo,
        activeIndex: 1001

      }
    },
    comments:{

    },
    mounted() {

    },
    methods:{

    }
  }
</script>

<style scoped>
/* #00a9fe */
  .warp {
    background: #000000;
    color: #ffffff;
  }
  .header {
    width: 1200px;
    height: 60px;
    margin: 0 auto;
    overflow: hidden;
  }
  .header-logo {
    /* width: 35%; */
    height: 60px;
    float: left;
  }
  .logo-img {
    display: block;
    width: 80px;
    height: 62px;
  }
  .header-company {
    float: left;
    padding: 0 10px;;
    height: 60px;
    font: 24px/60px '微软雅黑';
    
  }
  .header-nav {
    /* width: 65%; */
    float: left;
    overflow: hidden;
  }
  .headerLi {
    width: 140px;
    height: 60px;
    float: left;
    font: 18px/60px "微软雅黑";
    text-align: center;
    cursor: pointer;
  }
  .headerLi:hover {
    color: #ffffff;
    background: #00a9fe;
  }
  .active {
    color: #ffffff;
    background: #00a9fe;
  }
</style>


