<template>
	<div class="home">
   <pcIndex v-show="isPC"></pcIndex>
   <appIndex v-show="!isPC"></appIndex>
	</div>
</template>

<script>
  import pcIndex from './pc/index'
  import appIndex from './app/app-index'

	export default {
		name: "Home",
		data() {
			return {
        isPC: true
			}
		},
    components:{
      pcIndex,
      appIndex
    },
		mounted() {
      this.IspcOrapp();
		},
		methods: {
			/**
			* 获取客户端信息
			*/
      GetClientInfo() {
        var userAgentInfo = navigator.userAgent;
        var Agents = new Array("Android", "iPhone", "SymbianOS", "Windows Phone", "iPad", "iPod");
        var agentinfo = null;
        for (var i = 0; i < Agents.length; i++) {
            if (userAgentInfo.indexOf(Agents[i]) > 0) { agentinfo = userAgentInfo; break; }
        }
        if (agentinfo) {
            return agentinfo;
        } else {
            return "PC";
        }
			},
      // 控制代码切换
      IspcOrapp() {
        let browserType = this.GetClientInfo();
        if(browserType == 'PC') {
          this.isPC = true;
        }else{
          this.isPC = false;
        }
      }
		}
	}
</script>

<style>
</style>



