<template>
  <div class="function">
    <div class="fun-title">我们能做什么？</div>
    <div class="fun-block" v-for="item in functionList" :key="item.id">
      <img :src="item.url" alt="功能" class="fun-img">
      <div class="fun-name">{{item.name}}</div>  
    </div> 
  </div>
  
</template>

<script>
// 洛阳市畅易阁文化传播有限公司
  import fun01 from "../../../assets/image/fun01.jpg";
  import fun02 from "../../../assets/image/fun02.jpg";
  import fun03 from "../../../assets/image/fun03.jpg";
  import fun04 from "../../../assets/image/fun04.jpg";


  export default {
    data() {
      return {
        functionList: [
          { id: 1001, url: fun01, name: '八字' },
          { id: 1002, url: fun02, name: '六爻' },
          { id: 1003, url: fun03, name: '手相' },
          { id: 1004, url: fun04, name: '风水' }
        ],
      }
    },
    comments:{

    },
    mounted() {

    },
    methods:{

    }
  }
</script>

<style scoped>
/* #00a9fe */
  .function {
    margin: 0 auto;
    width: 1200px;
    overflow: hidden;
  }
  .fun-block {
    width: 25%;
    float: left;
  }
  .fun-img {
    display: block;
    margin: 0 auto;
    margin-top: 20px;
    width: 120px;
    height: 120px;
    border-radius: 50%;
  }
  .fun-name {
    margin: 0 auto;
    margin-top: 10px;
    width: 120px;
    font: 18px/32px "微软雅黑";
    text-align: center;
  }
  .fun-title {
    margin-top: 20px;
    margin-bottom: 20px;
    width: 100%;
    height: 60px;
    font: 26px/80px '微软雅黑';
    font-weight: bold;
    /* color:#00a9fe; */
    color:#333;
    text-align: center;
  }


</style>


